.Planner {
  @media only screen and (max-width: 1200px) {
    height: auto;
  }

  background-color: brown;
  height: 100vh;
  display: flex;

  @media only screen and (max-width: 1200px) {
    flex-direction: column;
  }

  header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    .logo {
      width: 42px;
      fill: white;
    }
  }

  main {

    @media only screen and (max-width: 1200px) {
      width: 100%;
      height: 100vh;
    }

    width: 100%;
    background-color: #242330;
    padding-top: 50px;
    padding-left: 50px;
    padding-right: 50px;
    padding-bottom: 50px;
    display: flex;
    flex-direction: column;

    .from-to-wrap {
      display: flex;
    }

    h1 {
      border: 0;
      background: none;
      color: white;
      font-size: 30px;
      font-weight: bold;
      margin-bottom: 10px;
    }

    p {
      color: #a7a7ac;
      font-size: 13px;
    }

    input {
      background: none;
      border: 0;
      color: #a7a7ac;
      margin-left: 5px;
      margin-right: 10px;
      width: 40px;
      font-family: 'Open Sans';
      font-size: 13px;


      &::placeholder {
        /* Chrome, Firefox, Opera, Safari 10.1+ */
        color: #a7a7ac;
        font-weight: bold;
      }

      &::-webkit-outer-spin-button,
      &::-webkit-inner-spin-button {
        /* display: none; <- Crashes Chrome on hover */
        -webkit-appearance: none;
        margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
      }

     
    }
  }

  #rect3223{
    opacity:1;
    fill:#ffffff;
    fill-opacity:1;stroke:red;stroke-width:2.07751179;stroke-linejoin:round;stroke-miterlimit:4;stroke-dasharray:none;stroke-opacity:1
}
}