aside {
  height: 100%;
  width: 400px;
  max-width: 400px;
  background-color: grey;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  background-color: white;

  @media only screen and (max-width: 1200px) {
    width: 100%;
    max-width: 100%;
    height: auto;
  }

  .content {
    height: 100%;
    width: 100%;
    padding: 50px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @media only screen and (max-width: 1200px) {
      flex-direction: row;
    }

    @media only screen and (max-width: 568px) {
      flex-direction: column;
    }

    h2 {
      margin-bottom: 20px;
      font-size: 14px;
      color: #242330;      
      text-transform: uppercase;
      font-weight: 800;
      letter-spacing: 1px;
      
      


      &:nth-child(5) {
        margin-top: 50%;
      }
    }

    

    .check-container {
      height: 100%;
      width: 100%;


      @media only screen and (max-width: 1200px) {
        margin-top: 0;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 50%;
      }

      @media only screen and (max-width: 568px) {
        margin-bottom: 50px;
        width: 100%;
      }
    }

    .line-container {

      
      @media only screen and (max-width: 1200px) {
        margin-top: 0;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      }

      height: 100%;
      margin-top: 50%;
      display: flex;
      flex-direction: column;

      display: none;

      .line-wrap {
        display: flex;
        align-items: center;

        .line {
          height: 5px;
          width: 80px;
          background-color: #DB4C40;
          margin-right: 20px;
          border-radius: 1000px;
        }

        input {
          border: 0;
          background: none;
          color: #2d519f;
          width: 100%;
          resize: none;
          overflow: hidden;
          height: auto;
          font-weight: bold;
          font-size: 12px;
          text-transform: uppercase;
        }
      }
    }
  } 

  footer {
    height: 100px;
    background-color: #191919;
    width: 100%;
    padding-left: 50px;
    padding-right: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #c7d1dd;
    cursor: pointer;

    &:hover {
      i {
        transform: translateX(10px);
      }
    }

    i {
      transition: ease 0.2s;
    }

    p {
      font-size: 12px;
      font-weight: bold;

      &:nth-child(1) {
        font-weight: bold;
        margin-bottom: 6px;
        color: white;
      }
    }
  }
}

.box {
  flex: 1;
  width: 100%;
}
