textarea {
    @media only screen and (max-width: 1200px) {
      width: auto;
    }

    border: 0;
    background: none;
    color: red;
    width: 100%;
    resize: none;
    overflow: hidden;
    height: auto;
    font-weight: bold;
    font-size: 12px;


    &::placeholder {
      /* Chrome, Firefox, Opera, Safari 10.1+ */
      color: #4f4e59;
    }
  }

  